.btn {
  display: inline-block;
  min-width: 280px;
  padding: 30px 10px 31px;
  font-family: $gilroy;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: -0.006em;
  text-align: center;
  text-transform: uppercase;
  &_blue {
    color: $blue; }
  &_solid {
    background-color: $textLight;
    transition: background-color $tr;
    @include hover {
      background-color: $white;
      &:active {
        background-color: $grey; } } } }
