body {
  line-height: 1.4;
  font-family: "Gilroy", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.56;
  letter-spacing: -0.005em;
  color: #394550;
}

.section {
  min-height: 100vh;
  height: 100%;
}

.section .inner {
  padding-top: 140px;
}

.section:nth-child(3) {
  background-image: linear-gradient(135deg, #FFF6B7 10%, #F6416C 100%);
}

.section:nth-child(4) {
  background-image: linear-gradient(135deg, #E2B0FF 10%, #9F44D3 100%);
}

.section:nth-child(5) {
  background-image: linear-gradient(135deg, #81FFEF 10%, #F067B4 100%);
}

a {
  color: inherit;
  text-decoration: none;
}

.outer {
  float: right;
  max-width: 1840px;
  padding: 0 30px;
  width: 100%;
}

.inner {
  padding-left: 77px;
  width: 100%;
}

.block {
  display: flex;
}

.title {
  font-size: 48px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: -0.2px;
}

.hero {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.hero__outer {
  height: 100%;
  padding-top: 93px;
  padding-bottom: 50px;
}

.header {
  position: fixed;
  top: 20px;
  left: 0;
  width: 100%;
  z-index: 20;
}

.header_first .header__logo {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__contacts {
  margin-left: 30px;
}

.header__tel {
  display: inline-block;
  vertical-align: middle;
  margin-right: 47px;
}

.nav__list {
  display: flex;
}

.nav__item {
  padding: 0 20px;
}

.nav__link {
  color: #1c2228;
}

.phone {
  font-size: 28px;
  font-weight: 700;
  letter-spacing: -0.1px;
  color: #ececec;
}

.logo img {
  display: block;
}

.btn {
  display: inline-block;
  min-width: 280px;
  padding: 30px 10px 31px;
  font-family: "Gilroy", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: -0.006em;
  text-align: center;
  text-transform: uppercase;
}

.btn_blue {
  color: #0067df;
}

.btn_solid {
  background-color: #ececec;
  transition: background-color 0.3s;
}

.no-touch .btn_solid:hover {
  background-color: #fff;
}

.no-touch .btn_solid:hover:active {
  background-color: #c1c1c1;
}
