//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$gilroy: 'Gilroy';

@font-face {
  @include fontdefault(normal, 300, $gilroy);
  @include fontpath('Gilroy-Light', 'Gilroy-Light', Gilroy-Light); }

@font-face {
  @include fontdefault(normal, 400, $gilroy);
  @include fontpath('Gilroy-Regular', 'Gilroy-Regular', Gilroy-Regular); }

@font-face {
  @include fontdefault(normal, 600, $gilroy);
  @include fontpath('Gilroy-SemiBold', 'Gilroy-SemiBold', Gilroy-SemiBold); }

@font-face {
  @include fontdefault(normal, 700, $gilroy);
  @include fontpath('Gilroy-Bold', 'Gilroy-Bold', Gilroy-Bold); }

@font-face {
  @include fontdefault(italic, 300, $gilroy);
  @include fontpath('Gilroy-LightItalic', 'Gilroy-LightItalic', Gilroy-LightItalic); }

@font-face {
  @include fontdefault(italic, 400, $gilroy);
  @include fontpath('Gilroy-RegularItalic', 'Gilroy-RegularItalic', Gilroy-RegularItalic); }

@font-face {
  @include fontdefault(italic, 600, $gilroy);
  @include fontpath('Gilroy-SemiBoldItalic', 'Gilroy-SemiBoldItalic', Gilroy-SemiBoldItalic); }

@font-face {
  @include fontdefault(italic, 400, $gilroy);
  @include fontpath('Gilroy-BoldItalic', 'Gilroy-BoldItalic', Gilroy-BoldItalic); }
